<template>
	<div>
    <el-row>
      <el-col :span="6" v-for="finding in raw" :offset="1" v-bind:key="finding.Id">
        <el-card class="box-card" shadow="hover" :body-style="{ padding: '0px' }" @click.native="toFinding(finding.Id)">
          <img v-bind:src="'http://35.240.33.107:8088/Storage/'+ getMedia(finding)">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{finding.UserNickname}}</div>
            <div class="box-name">{{finding.Name}}</div>
          </div>
        </el-card>
      </el-col>
    </el-row>  
  </div>
</template>

<script>
	import axios from 'axios'

  var storage = {}
  const api = axios.create({
    // baseURL: 'https://887c830a-6545-4ec1-8ec8-f3085930ba42.mock.pstmn.io/api',
    baseURL: 'http://35.240.33.107:8088/api',
  })
  export default {
    data() {
      return {
        raw: null,
      }
    },
    beforeRouteEnter(to, from, next) {
      const url = '/finding/inquiry/948a06f5-5f03-498a-9258-3f89ea1e2362'
      api.get(url)
      .then((response) => {
        storage = response.data;
        next()
      })
      .catch((error) => {
        console.log(error)
        this.$message({
          message: 'Network Error, Please try again',
          type: 'error'
        })
      })
    },
    created() {
      this.raw = storage;
    },
    methods: {
      toFinding(fid) {
        this.$store.dispatch('setFindingIdData', fid)
        this.$router.push({path: '/finding'})
      },
      getMedia(finding){
        if(finding.Dataset2[0].Media == null){
          return "default-finding-image.jpg";
        }
        return finding.Dataset2[0].Media+'.jpg';
      }
    }
  }
</script>

<style lang="scss">
  .box-card {
    margin:10px;
    width: 110%;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  .box-description {
      text-align: left;
      padding: 16px;
      .box-user {
        font-size: 19px;
        margin-bottom: 10px;
      }
      .box-name {
        margin-left: 25px;
        padding-left: 16px;
        font-size: 24px;
        padding: 0 !important;
      }
    }
</style>