<template>
	<div class="finding_wrap">
    <div class="finding-header">
      <label style="font-size: 30px; font-weight: bold; cursor: pointer; margin-right: 10px;"  @click="goBack()">&larr;</label>
      <i class="el-icon-user-solid"></i><label id="finding-username">{{finding.UserNickname}}</label>
      <p id="finding-name">{{finding.Name}}</p>
    </div>
    <div class="finding_content">
      <div class="finding_block">
        <img v-bind:src="'http://35.240.33.107:8088/Storage/'+ getMedia(finding)">
        <span id="finding-description">{{finding.Description}}</span>
        <img v-if="datasetIndex!=0" class="clickable" src="@/assets/left-arrow.png" @click="setDatasetIndex(-1)" style="position: absolute; bottom: 50%; left: 40px; width: 70px; height: 70px; border-radius: 50px;"/>
        <img v-if="datasetIndex!=finding.Dataset2.length-1" class="clickable" src="@/assets/right-arrow.png" @click="setDatasetIndex(+1)" style="position: absolute; bottom: 50%; right: 40px; width: 70px; height: 70px; border-radius: 50px;"/>
        <img src="@/assets/finding-label.png" style="position: absolute; bottom: 20%; left: 40px; width: 400px; height: auto; border-radius: 10px;"/>
        <label  style="position: absolute; bottom: 24%; left: 100px; width: 400px; height: auto; border-radius: 10px; font-size: 30px; font-weight: bold;"> {{finding.Dataset2[datasetIndex].Condition}} </label>

      </div>
      <div id="finding_comment">
        <p>{{comments.length}} Comments</p>
        <div class="comment_row">
          <i style="color: black;" class="el-icon-user-solid"></i>
          <span class="comment-user">{{username}}</span><br><br>
          <i v-bind:class="{'selected-comment':commentType==1}" class="el-icon-star-on comment-icon clickable" @click="enableCommentInput(1)"></i>
          <i v-bind:class="{'selected-comment':commentType==2}" class="el-icon-question comment-icon clickable" @click="enableCommentInput(2)"></i>
          <i v-bind:class="{'selected-comment':commentType==3}" class="el-icon-s-opportunity comment-icon clickable" @click="enableCommentInput(3)"></i>
          <el-input v-if="isCommentInputEnabled" v-model="commentValue"></el-input>
          <el-button v-if="isCommentInputEnabled" @click="postComment()">Post</el-button>
          <el-button style="width: 30px; padding-left:0; padding-right:0; background-color: grey;" v-if="isCommentInputEnabled" @click="isCommentInputEnabled=false; commentType=0;">X</el-button>
        </div>
        <div class="comment_row" v-for="comment in comments" v-bind:key="comment.Id">
          <i style="color: black;" class="el-icon-user-solid"></i>
          <span class="comment-user">{{comment.UserName}}</span>
          <br><br>
          <i v-if="comment.Type==1" class="el-icon-star-on comment-icon"></i>
          <i v-if="comment.Type==2" class="el-icon-question comment-icon"></i>
          <i v-if="comment.Type==3" class="el-icon-s-opportunity comment-icon"></i>
          <span class="comment-value">{{comment.Value}}</span>
          <br>
        </div>
      </div>
    </div>
    <div class="finding_list">
      <div v-if="parentFinding.Base">
        <div class="tab-selection" style="margin-right: 26px;" v-bind:class="{'selected-tab':selectedTab==1}">
          <span  class="clickable" @click="selectedTab=1;">{{replications.length}} Replications</span>
        </div>
        <div class="tab-selection" v-bind:class="{'selected-tab':selectedTab==2}">
          <span  class="clickable" @click="selectedTab=2;">{{variations.length}} Remixes</span>
        </div>
      </div>
      <div v-if="selectedTab==1 && !finding.Base">
        <div class="similar-block" style="margin-right: 26px;">
          <span>Similar results</span>
          <span><img class="similar-icon" src="@/assets/hface.png"/></span>
          <span><label class="similar-value">{{similarResults}}</label></span>
        </div>
        <div class="similar-block">
          <span>Different results</span>
          <span><img class="similar-icon" src="@/assets/cface.png"></span>
          <span><label class="similar-value">{{differentResults}}</label></span>
        </div>
      </div>

      <div v-if="!finding.Base" @click="replicateOrRemix()" class="replicate-remix-button"> {{selectedTab==1? 'Replicate': 'Remix'}} </div>

      <div id="replications-block" v-if="selectedTab==1 && finding.Type==1" style="width:100%; height: 700px;">
        <el-card v-for="replication in replications" v-bind:key="replication.id" style="margin-bottom: 20px;" class="box-card default-card clickable" shadow="hover" :body-style="{ padding: '0px' }" @click.native="loadFinding(replication.Id)">
          <img v-bind:src="'http://35.240.33.107:8088/Storage/'+ getMedia(replication)">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{replication.UserNickname}}</div>
            <div class="box-name">{{replication.Name}}</div>
          </div>
        </el-card>
      </div>
      <div id="original-finding-block" v-if="selectedTab==1 && parentFinding.Type==1" style="width:100%; height: 700px">
        <el-card style="margin-bottom: 20px;" class="box-card default-card clickable" shadow="hover" :body-style="{ padding: '0px' }" @click.native="loadFinding(parentFinding.Id)">
          <img v-bind:src="'http://35.240.33.107:8088/Storage/'+ getMedia(parentFinding)">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{parentFinding.UserNickname}}</div>
            <div class="box-name">{{parentFinding.Name}}</div>
          </div>
        </el-card>
      </div>
      <div id="variations-block" v-if="selectedTab==2" style="width:100%; height: 700px">
        <el-card v-for="variation in variations" v-bind:key="variation.id" style="margin-bottom: 20px;" class="box-card default-card clickable" shadow="hover" :body-style="{ padding: '0px' }" @click.native="loadFinding(variation.Id)">
          <img v-bind:src="'http://35.240.33.107:8088/Storage/'+ getMedia(variation)">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{variation.UserNickname}}</div>
            <div class="box-name">{{variation.Name}}</div>
          </div>
        </el-card>
        
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
  const api = axios.create({
    baseURL: 'http://35.240.33.107:8088/api',
  })
  export default {
    data() {
      return {
        username:"",
        finding:{Name:"", Description:"", Dataset2:[{Media:""}], Type:1, Base:null},
        parentFinding:{Name:"", Description:"", Dataset2:[{Media:""}], Type:0, Base:"_"},
        comments: [],
        replications: [],
        similarResults: 0,
        differentResults: 0,
        variations: [],
        selectedTab: 1,
        isCommentInputEnabled: false,
        commentValue: "",
        commentType: 0,
        datasetIndex: 0
      }
    },
    props: ['id'],
    created() {
      const findingId = sessionStorage.findingId;
      this.username = sessionStorage.name;
      api.get('/finding/'+findingId).then((response)=> {
        this.finding = response.data;
        if(this.finding.Base){
          api.get('/finding/'+this.finding.Base).then((response)=> {
            this.parentFinding = response.data;
            console.log(this.parentFinding);
          });
        }
      });
     
      api.get('/comment/finding/'+findingId).then((response) => {
        this.comments = response.data;
      });
      api.get('/finding/replication/'+findingId).then((response)=>{
        this.replications = response.data;
        this.calculateSimilarities();
      });
      api.get('/finding/variation/'+findingId).then((response)=>{
        this.variations = response.data;
      });
    },
    methods: {
      goBack() {
        this.$router.go(-1)
      },
      enableCommentInput(type){
          this.isCommentInputEnabled = true;
          this.commentType = type;
      },
      postComment(){
        const url = '/comment/';
        const comment = {
          userId : sessionStorage.uid,
          findingId: sessionStorage.findingId,
          value: this.commentValue,
          type: this.commentType
        }
        const config = {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }
        api.post(url, qs.stringify(comment),config).then((response)=>{
          var savedComment = response.data;
          if(!savedComment.UserName){savedComment.UserName = sessionStorage.name;} // fix this from backend and remove this line;
          this.isCommentInputEnabled = false;
          this.commentType = 0;
          this.commentValue = "";
          this.comments.push(response.data);
        });
      },
      getMedia(finding){
        if(finding.Dataset2.length<this.datasetIndex){
          return "default-finding-image.jpg";
        }
        if(!finding.Dataset2[this.datasetIndex]){
          return "default-finding-image.jpg";
        }
        if(finding.Dataset2[this.datasetIndex].Media == null || finding.Dataset2[this.datasetIndex].Media == ""){
          return "default-finding-image.jpg";
        }
        return finding.Dataset2[this.datasetIndex].Media+'.jpg';
      },
      replicateOrRemix(){
        if(this.selectedTab==1){
          alert("start replication");
        } else {
          alert("start remixing");
        }
      },
      calculateSimilarities(){
        this.similarResults = 0;
        this.differentResults = 0;
        this.replications.forEach((r)=>{
          if(r.Similarity == 1){
            this.similarResults++;
          } else if(r.Similarity == 2){
            this.differentResults++;
          }
        })
      },
      loadFinding(findingId){
        console.log("load finding...");
        this.$store.dispatch('setFindingIdData', findingId);
        window.location.reload(); //find the vue method to reload component
      },
      setDatasetIndex(value){
        if(this.finding.Dataset2.length==0){return;}
        if(this.datasetIndex+value<0){
          return;
        }
        if(this.datasetIndex+value == this.finding.Dataset2.length){
          return;
        }
        this.datasetIndex += value;
        this.reload();
      },
      reload() {
        this.$forceUpdate();
      }
    },

  }
</script>

<style lang="scss">
  .finding_wrap {
    margin: 90px 160px;
    p {
        font-size: 20px;
        font-weight: 600;
      }
  }
  .finding_content {
    display: inline-block;
    width: 60%;
    text-align: left; 
    border-right: 2px solid #B6B6BA;
    i {
      font-size: 22px;
      font-weight: 600;
    }
    img {
      width: 100%;
      height: auto;
    }
    .finding_block {
      position: relative;
      border-bottom: 2px solid #B6B6BA;
      margin-right: 30px;
      padding-bottom: 20px;
      span {
        display: block;

      }
    }
    #finding_comment {
      margin-right: 30px;
      .comment_row {
        border-bottom: 2px solid #E3E4E9;
        padding-top:20px;
        padding-bottom:20px;
        padding-left: 10px;
        .comment-user{
          font-size: 19px;
        }
        .comment-value{
          font-size: 24px;
        }
        .el-button{
          background-color: #2E2F32;
          color: white;
          font-size: 16px;
        }
        i {
          color: grey;
        }
      }
      p {
        font-size: 26px;
      }
      .el-input{
        font-size: 24px;
        width: 73%;
        display: inline-block;
      }
      .el-input_inner{
        border-radius: 50% !important;

      }
     
    }
  }
  #finding-name{
    margin-top: 20px;
    font-size: 33px;
  }
  #finding-username{
    font-size: 24px;
  }
  #finding-description{
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 25px;
  }
  .finding_list {
    display: inline-block;
    width: 30%;
    margin-left: 30px;
    vertical-align: top;
    text-align: center;
    // img{
    //   width: 85%;
    //   height: auto;
    // }
  }
  .similar-block {
    width: 47%; height: 130px; 
    background-color: #F3F5F9; 
    margin-top: 10px;
    margin-bottom: 10px; 
    display: inline-block; 
    vertical-align: top;
    font-size: 23px;
    .similar-icon{
      width: 90px;
      height: auto;
    }
    .similar-value{
      display: inline;
      font-size: 50px;
      font-weight: bold;
    }
  }
  .tab-selection {
    width: 47%; height: 30px; 
    display: inline-block; 
    vertical-align: top;
    font-size: 23px;
  }
  .selected-tab {
    border-bottom: 10px solid lightgrey;
    font-weight:bold;
  }
  .finding-tab {
    font-size: 24px;
    cursor: pointer;
  }
  .finding-tab:hover{
    font-weight: bold;
  }
  .replicate-remix-button{
    padding-top: 10px;
    width: 100%; 
    height: 40px; 
    background-color: #2E2F32; 
    color: white;
    font-size: 26px;
    cursor: pointer;
    display: inline-block;
    margin-top: 10px;
    margin-bottom: 20px;
  }
  .finding-header{
    margin-left: 70px;
    text-align: left;
  }
  .clickable{
    cursor: pointer;
  }
  .back-button{
    font-size: 2rem;
    padding: 0;
    width: 10px;
    border: none !important;
    outline: none !important;
  }
  .el-icon-user-solid{
    font-size: 1.5rem;
    border-radius: 50%;
    background-color: lightgray;
    padding: 5px;
    margin-right: 10px;
  }
  .comment-icon{
    font-size: 2rem !important;
    margin-right: 10px;
  }
  .default-card {
    width: 100% !important;
    margin-left: 0 !important;
  }
  .selected-comment {
    color:#2E2F32 !important;
    // background-color: #2E2F32;
  }
</style>