import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import Discover from '@/components/Discover.vue'
import Learn from '@/components/Learn.vue'
import MyInquiries from '@/components/MyInquiries.vue'
import Find_AR from '@/components/Find_AR.vue'
import Finding from '@/components/Finding.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/home',
    name: 'Home',
    component: Home,
    children:[
    {
      path: 'discover',
      component: Discover
    },
    {
      path: 'learn',
      component: Learn
    },
    {
      path: 'myinquiries',
      component: MyInquiries
    }]
  },
  {
    path: '/find_ar',
    name: 'Find_AR',
    component: Find_AR
  },
  {
    path: '/finding',
    name: 'Finding',
    component: Finding
  }
]

const router = new VueRouter({
  routes,
  mode: 'hash',
  base: process.env.BASE_URL,
})

export default router
